"use server";
import { initializeApp } from "firebase/app";
import {
  getAuth,
  GoogleAuthProvider,
  signInWithPopup,
  signOut,
} from "firebase/auth";
import { getFirestore, doc, setDoc, getDoc } from "firebase/firestore";


interface UserData {
  sub: string; // Firebase UID used as the document ID
  fullName: string;
  email: string;
  email_verified: boolean;
  picture: string;
  subscription_status: string;
  user_created_at: Date;
}


const firebaseConfig = {
  apiKey: process.env.API_KEY,
  authDomain: process.env.AUTH_DOMAIN,
  databaseURL: process.env.DATABASE_URL,
  projectId: process.env.PROJECT_ID,
  appId: process.env.APP_ID,
  measurementId: process.env.MEASUREMENT_ID,
};

const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);
const googleProvider  = new GoogleAuthProvider()
 
// create document
async function createUserDocument(userData: UserData) {
  try {
    // Create a reference to the document in the "users" collection with the ID as userData.sub
    const userRef = doc(db, "users", userData.sub);
    // Check if the document already exists
    const userDoc = await getDoc(userRef);
    if (!userDoc.exists()) {
      // Document doesnâ€™t exist, so create it
      await setDoc(userRef, userData);
      // Add a 1-second delay to allow Firestore to finish any pending tasks
      await new Promise((resolve) => setTimeout(resolve, 500));
    }
  } catch (error) {
    console.error("Error creating user document:", error);
    throw error; // Re-throw the error for handling by the caller
  }
}

async function createUserAccount() {
  try {
    const result = await signInWithPopup(auth, googleProvider);
    const credential = GoogleAuthProvider.credentialFromResult(result);
    const idToken = credential?.idToken;
    const user = result.user;
    const userData = {
      sub: user.uid, // Firebase UID used as the document ID
      fullName: user.displayName || "",
      email: user.email || "",
      email_verified: user.emailVerified,
      picture: user.photoURL || "",
      subscription_status: "Free",
      user_created_at: new Date(),
    };
    await createUserDocument(userData);
    // Generate a one time code
    // const code = crypto.randomBytes(16).toString("hex");

    if (window !== undefined) {
      window.location.href = "pingprospect://" + idToken;
    }
    return idToken;
  } catch (error) {
    console.error("Sign-in error:", error);
    throw error;
  }
}

async function SiginOut() {
  try {
    await signOut(auth);
  } catch (error) {
    throw error;
  }
}


export {app ,auth ,createUserAccount ,SiginOut};